import React from 'react';
import { useState, useEffect } from "react";
import { Timer } from 'easytimer.js';
import logo from './logo.svg';
import './App.css';

function App() {


  const [currentPushups, setCurrentPushups] = useState(0);
  const [maxPushups, setMaxPushups] = useState(10);
  const [isComplete, setIsComplete] = useState(0);
  const [pushupTimeRemaining, setPushupTimeRemaining] = useState(3)
  const [visualTimer, setVisualTimer] = useState(0)

  

  function tap(event){
    document.removeEventListener('click', tap, true);
    if(isComplete == 0){
      setPushupTimeRemaining(3);
      setCurrentPushups(currentPushups + 1)
    }
  }

  function subtractSecond(){
    setPushupTimeRemaining(pushupTimeRemaining - 1)
  }

  useEffect( ()=>{
    if (currentPushups > maxPushups){
      setMaxPushups(currentPushups)
    }
  },[currentPushups])

  useEffect( ()=>{
    document.removeEventListener('click', tap, true);
    clearTimeout(visualTimer)
    console.log("Done")
    console.log("isComplete: "+ isComplete)
    
  },[isComplete])

  useEffect( ()=>{

    clearTimeout(visualTimer)

    // document.addEventListener('click', (tap), true); 

    if (pushupTimeRemaining > 0){
      setVisualTimer(setTimeout(subtractSecond, 1000));
    }else{
      setIsComplete(1)
      document.removeEventListener('click', tap, true);
    }
    
    
  },[pushupTimeRemaining])

  

  return (
    <div className="App" onClick={tap} style={{background: "red", height: "100vh"}}>
      <h1>Max Pushups: {maxPushups}</h1>
      <h1>Current Pushups: {currentPushups}</h1>
      <h1>{pushupTimeRemaining}</h1>
    </div>
  );
}

export default App;
